<template>
  <div v-if="rowModel[column.name]" class="cell-options">
    <component
      :is="column.tooltip ? 'Tooltip' : 'div'"
      v-for="el in column.data"
      :key="el"
      :text="(column.tooltip||{})[el] ? t((column.tooltip||{})[el]) : ''"
      class="cell-options__item"
    >
      <component
        v-model="rowModel[column.name][el]"
        :is="el + '-check'"
      />
    </component>
  </div>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { useModel } from '@/shared/utils/useModel'
import { defineAsyncComponent, onMounted } from 'vue'
import { TABLE_TYPES } from '@/admin/common/tableTypes'

export default {
  name: 'CellOptions',
  components: {
    Tooltip: defineAsyncComponent(() => import('@/shared/components/UIKit/Tooltip')),
    PinCheck: defineAsyncComponent(() => import('@/admin/components/Table/CellFormatter/PinCheck')),
    CommentsCheck: defineAsyncComponent(() => import('@/admin/components/Table/CellFormatter/CommentCheck'))
  },
  props: {
    column: {
      default: () => ({})
    },
    row: {
      default: () => ({})
    },
    item: {
      default: ''
    }
  },
  setup (props, { emit }) {
    const { t } = useI18n()
    const { value: rowModel } = useModel(props, emit, { prop: 'row' })

    onMounted(() => {
      if (props.column.type === TABLE_TYPES.OPTIONS) {
        setOptions()
      }
    })

    const setOptions = () => {
      if (!rowModel.value[props.column.name]) {
        rowModel.value[props.column.name] = props.column.data.reduce((acc, item) => {
          return {
            ...acc,
            [item]: false
          }
        }, {})
      }
    }

    return {
      t,
      rowModel
    }
  }
}
</script>

<style lang="scss">
.cell-options {
  display: flex;
  align-items: center;

  &__item:not(:last-of-type) {
    margin-right: 20px;
  }
}
</style>
